import React from "react";
import { PostSkeleton } from "./Skeleton";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { fetchRemovePost } from "../../redux/slices/posts";

import styles from "./Post.module.scss";

import clsx from 'clsx';
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import EyeIcon from '@mui/icons-material/RemoveRedEyeOutlined';
import CommentIcon from '@mui/icons-material/ChatBubbleOutlineOutlined';

export const Post = ({
    _id,
    title,
    subtitle,
    createdAt,
    imageUrl,
    user,
    viewsCount,
    commentsCount,
    tags,
    theme,
    children,
    isFullPost,
    isLoading,
    isEditable
}) => {
    const dispatch = useDispatch();
    if(isLoading) {
        return <PostSkeleton />
    }

    const onClickRemove = () => {
        if(window.confirm("    ?")) {
            dispatch(fetchRemovePost(_id));
        }
    }


    return (
        isFullPost ? (
            <div className={clsx(styles.root, { [styles.rootFull]: isFullPost })}>
                <p className={styles.title}>{title}</p>
                <p className={styles.author_text}><span className={styles.theme_text}>{theme}</span>  {createdAt}   {user}</p>
                <p className={styles.subtitle}>{children}</p>
            </div>
        ) : (
            <Link to={`/posts/${_id}`} className={styles.link}>
                <div className={clsx(styles.root, { [styles.rootFull]: isFullPost })}>
                    {isEditable && (
                        <div className={styles.editButtons}>
                            <Link to={`/posts/${_id}/edit`}>
                                <IconButton color="primary">
                                    <EditIcon />
                                </IconButton>
                            </Link>
                            <button onClick={onClickRemove}>
                                <IconButton color="secondary">
                                    <DeleteIcon />
                                </IconButton>
                            </button>
                        </div>
                    )}
                    {imageUrl && (
                        <>
                            <img className={clsx(styles.image, { [styles.imageFull]: isFullPost })} src={imageUrl} alt={title} />
                            <img className={clsx(styles.imageCopy, { [styles.imageFullCopy]: isFullPost })} src={imageUrl} alt={title} />
                        </>
                        
                    )}
                    <div className={styles.wrapper}>
                        <p className={styles.author_text}><span className={styles.theme_text}>{theme}</span>   {user}</p>
                        <p className={styles.title}>{title}</p>
                        <p id={styles.postText} className={styles.subtitle}>{subtitle}</p>
                    </div>
                </div>
            </Link>
        )
        
    )
}