import Container from "@mui/material/Container";
import { useEffect, useState } from "react";
import { Routes, Route } from "react-router-dom";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { Navigate } from "react-router-dom";

import { Header } from "./components/Header";
import { Footer } from "./components/Footer";
import { Home } from "./pages";
import { FullPost } from "./pages/FullPost";
import { AddPost } from "./pages/AddPost";
import { Login } from "./components/Login";

import styles from "./index.scss"
import { fetchAuth, fetchAuthMe, fetchRegister, selectIsAuth } from "./redux/slices/auth";

function App() {
  // Login
  const isAuth = useSelector(selectIsAuth);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchAuthMe())
  }, [])

  const { register, handleSubmit, setError, formState: { errors, isValid } } = useForm({
    defaultValues: {
      email: "kadirovdenis123@gmail.com",
      password: "denis123",
      fullName: "kadirovdenis123"
    },
    mode: "onChange"
  });

  const onSubmitLogin = async (values) => {
    const data = await dispatch(fetchAuth(values));

    if(!data.payload) {
      alert("  ");
    }

    else if('token' in data.payload) {
      window.localStorage.setItem("token", data.payload.token);
      scrollFunc();
    }
  }

  const onSubmitRegister = async (values) => {
    const data = await dispatch(fetchRegister(values));

    if(!data.payload) {
      alert("  ");
    }

    else if('token' in data.payload) {
      window.localStorage.setItem("token", data.payload.token);
      scrollFunc();
    }
  }

  const [isActiveState, setActive] = useState(false);
  
  const scrollFunc = () => {
    document.body.classList.remove('hidden');
    document.body.classList.add('scroll');
    setActive(false); 
  }
  const hiddenFunc = () => {
    document.body.classList.remove('scroll');
    document.body.classList.add('hidden');
    setActive(true);
  }


  return (
    <Container maxWidth="lg">
      <Header onChange={hiddenFunc}/>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/posts/:id" element={<FullPost />} />
        <Route path="/add-post" element={<AddPost />} />
        <Route path="/posts/:id/edit" element={<AddPost />} />
      </Routes>
      <Login isActive={isActiveState} onChange={scrollFunc} onSubmitLogin={handleSubmit(onSubmitLogin)} onSubmitRegister={handleSubmit(onSubmitRegister)} register={register}/>
      <Footer />
    </Container>
  );
}

export default App;
