import React from "react";
import SimpleMDE from 'react-simplemde-editor';

import styles from "./AddPost.module.scss";
import 'easymde/dist/easymde.min.css';
import { selectIsAuth } from "../../redux/slices/auth";
import { useNavigate, Navigate, useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import axios from "../../axios";

export const AddPost = () => {
    const { id } = useParams();

    const navigate = useNavigate();
    const isAuth = useSelector(selectIsAuth);
    const [ loading, setLoading] = React.useState(false);
    const [text, setText] = React.useState('');
    const [title, setTitle] = React.useState('');
    const [tags, setTags] = React.useState([]);
    const [imageUrl, setImageUrl] = React.useState('');
    const inputFileRef = React.useRef(null);

    const handleChangeFile = async (e) => {
        try {
            const formData = new FormData();
            formData.append("image", e.target.files[0]);
            const { data } = await axios.post("http://localhost:3333/upload", formData);
            setImageUrl(data.url);
        } catch (error) {
            console.warn(error);
            alert("   ");
        }
        console.log(e.target.files);
    };

    const onClickRemoveImage = async () => {
        setImageUrl('');
    };

    const onChange = React.useCallback((value) => {
        setText(value);
    }, []);

    const options = React.useMemo(
        () => ({
            spellChecker: false,
            maxHeight: '400px',
            autofocus: true,
            placeholder: ' ...',
            status: false,
            autosave: {
                enabled: true,
                delay: 1000,
            }
        }),
        []
    );

    const onSubmit = async () => {
        try {
            setLoading(true);

            const fields = {
                title, 
                imageUrl,
                tags: tags.split(" "),
                text
            }

            const { data } = await axios.post("/posts", fields);

            const id = data._id;

            navigate(`/posts/${id}`);
        } catch (error) {
            console.warn(error); 
        }
    } 

    if(!window.localStorage.getItem("token") && !isAuth) {
        return <Navigate to="/"/>
    }

    React.useEffect(() => {
        if(id) {
            axios.get(`/posts/${id}`).then(({ data }) => {
                setTitle(data.title);
                setText(data.text);
                setTags(data.tags.join(' '));
                setImageUrl(data.imageUrl);
            }).catch(error => {
                console.log(error);
                alert("   ");
            })
        }
    }, [id])

    React.useEffect(() => PageTransitionEvent, []);

    return (
        <div className={`${styles.root} content`}>
            <div className={styles.content}>
                <input className={styles.header_text} type="text" placeholder=" .." value={title} onChange={(e) => setTitle(e.target.value)}/>
                <input className={styles.input} type="text" placeholder=" ( )" value={tags} onChange={(e) => setTags(e.target.value)}/>
                <button onClick={() => inputFileRef.current.click()} className={styles.upload_button}>
                     
                </button>                
                {imageUrl && (
                    <>
                        <button onClick={onClickRemoveImage} className={styles.upload_button}></button> 
                        <img className={styles.image} src={`http://localhost:3333${imageUrl}`} alt="Uploaded" />
                    </>
                )}
                <div>
                    <input className={styles.inputFileRef} hidden type="file" onChange={handleChangeFile} ref={inputFileRef}/>
                </div>
                
                <SimpleMDE className={styles.editor} value={text} onChange={onChange} options={options} />
                <div className={styles.buttons}>
                    <button onClick={onSubmit} className={styles.publish_button}>
                        <p></p>
                    </button>
                    <button className={styles.cancel_button}>
                        <p></p>
                    </button>
                </div>
            </div>
        </div>
    )
}   