import React from "react";
import { useParams } from "react-router-dom";

import axios from "../../axios";

import styles from "./FullPost.module.scss";
import { Post } from "../../components/Post";
import { Comments } from "../../components/Comments";
import { AddComment } from "../../components/AddComment";
import ReactMarkdown from "react-markdown";

export const FullPost = () => {
    const [ data, setData ] = React.useState();
    const [ isLoading, setLoading ] = React.useState(true);
    const { id } = useParams();

    React.useEffect(() => {
        axios.get(`/posts/${id}`).then(res => {
            setData(res.data);
            setLoading(false);
        }).catch((err) => {
            console.warn(err);
            alert("   ");
        });
    }, [])

    if(isLoading) {
        return <Post isLoading/>
    }
    return (
        <div className={`${styles.root} content`}>
            <Post
                _id={data._id}
                title={data.title}
                user={data.user.fullName}
                createdAt={data.createdAt}
                theme={data.tags[0]}
                isEditables
                isFullPost>
                    <ReactMarkdown children={data.text}/>
            </Post>
            <Comments
                items={[
                    {
                        user: {
                            fullname: "Kadirov Denis",
                            avatarUrl: "https://i.ibb.co/kML9B7K/avataaars.png"
                        },
                        text: "Unfortunately I'm not a Mu scovite but a Tyumen",
                        date: "June 20, 2022"
                    },
                    {
                        user: {
                            fullname: "Vasya Pupkin",
                            avatarUrl: "https://i.ibb.co/jh8YW32/avataaars-1.png"
                        },
                        text: "Its officialIm letting you style me next time we go out.",
                        date: "June 20, 2022"
                    },
                    {
                        user: {
                            fullname: "Alexander Kazantsev",
                            avatarUrl: "https://i.ibb.co/zh9Y9yW/avataaars-2.png"
                        },
                        text: "How is it even possible to look this cool?This look belongs on the red carpet!Where are the paparazzi?Putting this picture on my mood board.Please start a vlog so I can use it for inspo.Best #OOTD Ive ever seen!",
                        date: "June 20, 2022"
                    }
                ]}
            >
                <AddComment />
            </Comments>
            
        </div>
    )
}